<template>

	<div class="chedui-2">
		<div class="chedui-top clearfix">
			<p><span>证件查询 / </span><span>说明说明说明</span></p>
		</div>
		<div class="chedui-bottom">
			<div class="zhengjian-box clearfix">
				<div class="zj-left">
					<div class="zj-left-top">
						身份证号：
						<el-input v-model="input" placeholder="请输入身份证号" style='width: 225px;'></el-input>
						&nbsp;&nbsp;姓名：
						<el-input v-model="input" placeholder="请输入要查询的姓名" style='width: 225px;'></el-input>
					</div>
					<div class="zj-left-bottom clearfix">
						<el-upload action="https://jsonplaceholder.typicode.com/posts/" list-type="picture-card" :on-preview="handlePictureCardPreview" :on-remove="handleRemove">
							<i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog v-model="dialogVisible" size="tiny">
							<img width="100%" :src="dialogImageUrl" alt="">
						</el-dialog>
						<div class="haha">
							<p>发证机关：<span>姓名、</span><span>车牌号、</span><span>手机号、</span></p>
							<p>出生年月：<span>侏罗纪时代^ - ^</span></p>
						</div>
					</div>
				</div>
				<div class="zj-right">
					<el-button type="primary" icon="search">查询</el-button>
					<el-button type="primary" icon="delete">清除</el-button>
					<el-button type="primary" icon="upload">打印</el-button>
				</div>
			</div>
			<div class="lishi-box clearfix">
				<div class="li-left">
					<span>历史记录</span>
					<span>特别提醒：只提供半年内的验证记录</span>
				</div>
				<div class="li-right">
					<div class="block">
						<span class="demonstration">起始日期</span>
						<el-date-picker v-model="value1" type="date" placeholder="选择开始日期" :picker-options="pickerOptions0">
						</el-date-picker> -- 
						<el-date-picker v-model="value2" type="date" placeholder="选择结束日期" :picker-options="pickerOptions0">
						</el-date-picker>
						<el-button type="primary" icon="search">查询</el-button>
					</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	//	import chaxunBar from '@/components/chaxunBar';
	export default {
		components: {
			//			chaxunBar
		},
		data() {
			return {
				input: '',
				dialogImageUrl: '',
				dialogVisible: false,
				value1: '',
				value2: '',
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
			}
		},
		methods: {
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			}
		}
	}
</script>

<style lang="less">
	.clearfix:after {
		display: block;
		clear: both;
		content: '';
	}
	
	.chedui-2 {
		.chedui-top {
			width: 1340px;
			p:nth-child(1) {
				height: 90px;
				line-height: 90px;
				color: #8F9DC0;
				float: left;
				span:nth-child(1) {
					font-size: 20px;
					font-weight: bold;
					color: #3651A2;
				}
				span:nth-child(2) {
					font-size: 14px;
				}
			}
		}
		.chedui-bottom {
			width: 1340px;
			background: #FFFFFF;
			border: 1px solid #CDCDCD;
			text-align: center;
			padding-bottom: 500px;
			.zhengjian-box {
				margin: 40px 30px 30px 30px;
				padding-bottom: 30px;
				border-bottom: 1px solid #ccc;
				.zj-left {
					float: left;
					.zj-left-top {
						font-size: 16px;
						color: #8791B5;
						>.el-input {
							>input {
								background: #F5F6FB;
							}
						}
					}
					.zj-left-bottom {
						>div {
							float: left;
							margin-top: 25px;
							>div {
								width: 300px;
								height: 200px;
								>i {
									line-height: 200px;
								}
							}
						}
						>.haha {
							float: left;
							margin: 40px 0 0 60px;
							color: #8791B5;
							>p {
								text-align: left;
								margin-bottom: 10px;
								font-size: 16px;
							}
						}
					}
				}
				.zj-right {
					float: right;
					>button {
						width: 100px;
					}
				}
			}
			.lishi-box {
				margin: 40px 30px 30px 30px;
				.li-left {
					float: left;
					line-height: 40px;
					>span:nth-child(1) {
						font-size: 20px;
						font-weight: bold;
						color: #3651A2;
					}
					>span:nth-child(2) {
						font-size: 12px;
						color: #FF0000;
					}
				}
				.li-right {
					float: right;
					>div{
						>span{
							color: #3651A2;
						}
						>div{
							width: 155px;
							>input{
								width: 155px;
								height: 35px;
								background: #F5F6FB;
							}
						}
					}
				}
			}
		}
	}
</style>


